import React, { useEffect, useState } from "react";
import { Button } from "./Button";
import "../stylesheets/HeroSection.css";
import { BsPlayCircle } from "react-icons/bs";

function HeroSection() {
    const [popup, setPopup] = useState(false);
    const [playPause, setplayPause] = useState(false);

    useEffect(() => {
        const trailer = document.querySelector("#trailer");
        playPause ? trailer.play() : trailer.pause();
    });

    const handlePlayPause = () => {
        setplayPause(!playPause);
    };
    
    const handlePopup = () => {
        setPopup(!popup);
        popup ? setplayPause(false) : setplayPause(true);
    };

    return (
        <div className="hero-container">
            <h1>
                RAIDEN SHOGUN
            </h1>
            <p>Now, you shall perish!</p>
            <div className="hero-btns">
                <Button
                    className="btn"
                    buttonStyle="btn--outline"
                    buttonSize="btn--large"
                >
                    DIVE IN
                </Button>
                <Button
                    className="btn"
                    buttonStyle="btn--fill"
                    buttonSize="btn--large"
                    onClick={handlePopup}
                >
                    WATCH TRAILER <BsPlayCircle className="play-icon" />
                </Button>
            </div>
            <div
                className={popup ? "overlay active" : "overlay"}
                onClick={handlePopup}
            ></div>
            <div className={popup ? "pop-up active" : "pop-up"}>
                <video
                    id="trailer"
                    src="/videos/Ei.mp4"
                    onClick={handlePlayPause}
                    loop
                />
            </div>
        </div>
    );
}

export default HeroSection;
