import { useEffect } from "react";

function Particles() {
    useEffect(() => {
        const canvas = document.createElement("canvas");
        const ctx = canvas.getContext("2d");

        canvas.width = window.innerWidth;
        canvas.height = window.innerHeight;
        canvas.style.position = "absolute";
        canvas.style.top = "0";
        canvas.style.left = "0";

        const container = document.querySelector(".particles");
        // container.style.background = `url(${canvas.toDataURL()})`;
        container.appendChild(canvas);

        let particleArray;

        class Particle {
            constructor(x, y, dirX, dirY, size, color) {
                this.x = x;
                this.y = y;
                this.dirX = dirX;
                this.dirY = dirY;
                this.size = size;
                this.color = color;
                this.scale = 0.1;
                this.counter = 0;
            }
            draw() {
                var gradient = ctx.createRadialGradient(
                    this.x,
                    this.y,
                    this.size / 8,
                    this.x,
                    this.y,
                    this.size
                );
                gradient.addColorStop(0, "white");
                gradient.addColorStop(1, "white");

                ctx.beginPath();
                ctx.arc(this.x, this.y, this.size, 0, Math.PI * 2, false);
                ctx.shadowColor = this.color;
                ctx.shadowBlur = 10;
                ctx.fillStyle = this.color;

                ctx.fill();
            }
            update() {
                if (
                    this.x + this.size > canvas.width ||
                    this.x - this.size < 0
                ) {
                    this.dirX = -this.dirX;
                }
                if (
                    this.y + this.size > canvas.height ||
                    this.y - this.size < 0
                ) {
                    this.dirY = -this.dirY;
                }

                this.size += this.scale;
                this.counter += 0.1;

                if (this.counter > 3) {
                    this.scale = -this.scale;
                    this.counter = 0;
                }

                this.x += this.dirX;
                this.y += this.dirY;

                this.draw();
            }
        }

        function init() {
            particleArray = [];
            for (let i = 0; i < 25; i++) {
                let size = Math.random() * 3;
                let x = Math.random() * (window.innerWidth - size * 2);
                let y = Math.random() * (window.innerHeight - size * 2);
                let dirX = Math.random() * 0.4 - 0.2;
                let dirY = Math.random() * 0.4 - 0.2;
                let colors = ["#721240"];
                let color = colors[Math.floor(Math.random() * colors.length)];

                particleArray.push(new Particle(x, y, dirX, dirY, size, color));
            }
        }

        function animate() {
            requestAnimationFrame(animate);
            // container.style.background = `url(${canvas.toDataURL()})`;
            ctx.clearRect(0, 0, window.innerWidth, window.innerHeight);

            for (let i = 0; i < particleArray.length; i++) {
                particleArray[i].update();
            }
        }

        init();
        animate();

        window.addEventListener("resize", () => {
            canvas.width = window.innerWidth;
            canvas.height = window.innerHeight;
            init();
        });

        return () => {
            window.removeEventListener("resize");
        };
    }, []);

    return null;
}

export default Particles;
