import React, { useState, useEffect, useRef } from "react";
import "../stylesheets/NavBar.css";
import { Link } from "react-router-dom";
import { ReactComponent as MenuIcon } from "../assets/menu.svg";
import { ReactComponent as CloseIcon } from "../assets/close.svg";
import { Button } from "./Button";
import { ReactComponent as Eternity } from "../assets/eternity.svg";
import { ReactComponent as Electro } from "../assets/electro.svg";
import anime from "animejs";

function NavBar() {
    const [click, setClick] = useState(false);
    const [show, setShow] = useState(false);
    const [button, setButton] = useState(true);

    const handleClick = () => setClick(!click);
    const closeMobileMenu = () => setClick(false);

    const showButton = () => {
        if (window.innerWidth <= 960) {
            setButton(false);
        } else {
            setButton(true);
        }
    };

    useEffect(() => {
        showButton();
    }, []);

    useEffect(() => {
        const timeout = setTimeout(() => {
            setShow(click);
        }, 500);

        return () => clearTimeout(timeout);
    }, [click]);

    const animation = useRef(null);

    useEffect(() => {
        animation.current.addEventListener("click", () => {
            const timeline = anime.timeline({
                duration: 500,
                easing: "easeOutExpo",
            });

            timeline
                .add({
                    targets: animation.current,
                    rotate: 720,
                    opacity: 0,
                    scale: 0.25,
                })
                .add({
                    rotate: 360,
                    targets: animation.current,
                    opacity: 1,
                    scale: 1,
                });
        });
    }, []);

    return (
        <nav className="navbar">
            <div className="navbar-container">
                <Electro className="electro" />
                <Link to="/" className="navbar-logo" onClick={closeMobileMenu}>
                    <Eternity className="eternity" />
                </Link>
                <div
                    className={click ? "menu-icon active" : "menu-icon"}
                    onClick={handleClick}
                    ref={animation}
                >
                    {show ? <MenuIcon /> : <CloseIcon />}
                </div>
            </div>
            <div className={click ? "nav-menu active" : "nav-menu"}>
                <ul className="nav-list">
                    <li className="nav-item">
                        <Link
                            to="/"
                            className="nav-link"
                            onClick={closeMobileMenu}
                        >
                            Home
                        </Link>
                    </li>
                    <li className="nav-item">
                        <Link
                            to="/about"
                            className="nav-link"
                            onClick={closeMobileMenu}
                        >
                            About
                        </Link>
                    </li>
                    <li className="nav-item">
                        <Link
                            to="/gallery"
                            className="nav-link"
                            onClick={closeMobileMenu}
                        >
                            Gallery
                        </Link>
                    </li>
                    <li className="nav-item-mobile">
                        <Link
                            to="/contact-us"
                            className="nav-link"
                            onClick={closeMobileMenu}
                        >
                            Contact Us
                        </Link>
                    </li>
                </ul>
                <Link to="/contact-us" className="btn-mobile">
                    {button && (
                        <Button buttonStyle="btn--outline">Contact Us</Button>
                    )}
                </Link>
            </div>
        </nav>
    );
}

export default NavBar;
