import React from "react";
import { Link } from "react-router-dom";

function CardItem(props) {
    return (
        <li className="card" id={props.i} style={{"--i": props.i}}>
            <figure>
                <img src={props.src} alt="Idk" />
            </figure>
        </li>
    );
}

export default CardItem;
